* {
  margin: 0;
  padding: 0;
}

body {
  max-width: 1920px;
  border: 7px solid rgb(31, 61, 95);
  margin: 15px;
  border-radius: 10px;
}

img {
  width: 320px;
  height: 200px;
  border-radius: 5px;
}

p {
  font-size: 12px;
  letter-spacing: 1.3;
  font-family: 'Courier New', Courier, monospace;
  color: white;
  text-align: center;
}
button {
  cursor: pointer;
}
.search-form {
  padding: 20px 0;
}

.gallery {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  background-color: rgb(54, 117, 219);
}

.info {
  display: flex;
  flex-direction: column;
  align-items: start;
  gap: 5px;
}

.search-form-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(54, 117, 219);
}
.search-form :placeholder-shown {
  text-align: center;
  background-color: lightgray;
  text-transform: none;
}

input[type='text'] {
  text-align: center;
  padding: 0;
  margin: 0 auto;
  letter-spacing: 1.3;
  text-transform: uppercase;
  color: rgb(23, 82, 74);
}

.search-btn {
  margin-left: 15px;
  padding: 0 15px;
}

.load-more-wrapper {
  display: flex;
  flex-direction: column;
  gap : 10px;
  align-items: center;
  justify-content: center;
  padding: 30px;
  background-color: rgb(54, 117, 219);
}

.load-more-btn {
  padding: 5px 20px;
}

.photo-card {
  border: 2px solid #333;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 2 2 20px rgb(145, 170, 228);
  background-color: #a9c0f9;
  width: 320px;
}

.info {
  display: flex;
  flex-direction: row;
}

.sl-image {
  border: 15px solid #282456;
  border-radius: 10px;
  padding: 10px;

  box-shadow: 5 5 20px rgb(145, 170, 228);
  background-color: #a9c0f9;
}
